import React, { Fragment } from 'react';
import { Table } from 'react-bootstrap';

export default function InforDetailItemComponent(props) {
    var dataProduct = props.thongTinProduct;
    var dataBrand = props.thongTinBrand;

    return (
        <Fragment>
            <div className="row" style={{ marginTop: 40, height: 'auto' }}>
                <div className="col">
                    <h5>THNG TIN CHI TIT</h5>
                    <div className="col-sm-9 infor-detail-item">
                        <Table className="table-infor-detail-item" bordered>
                            <tbody>
                                <tr>
                                    <td className="title">Thng hiu</td>
                                    <td>{dataBrand.ten}</td>
                                </tr>
                                <tr>
                                    <td className="title">Xut x</td>
                                    <td>{dataBrand.xuatXu}</td>
                                </tr>
                                <tr>
                                    <td className="title">Ni sn xut</td>
                                    <td>{dataProduct.noiSanXuat}</td>
                                </tr>
                                {
                                    dataProduct.thongTinBaoHanh.baoHanh === false && (
                                        <tr>
                                            <td className="title">Bo hnh</td>
                                            <td>Khng</td>
                                        </tr>
                                    )
                                }

                                {
                                    dataProduct.thongTinBaoHanh.baoHanh === true && (
                                        <Fragment>
                                            <tr>
                                                <td className="title">Bo hnh</td>
                                                <td>C</td>
                                            </tr>

                                            <tr>
                                                <td className="title">Loi bo hnh</td>
                                                <td>{dataProduct.thongTinBaoHanh.loaiBaoHanh === 0 ? 'Bo hnh chnh hng' : 'Bo hnh t shop'}</td>
                                            </tr>

                                            <tr>
                                                <td className="title">Thi gian bo hnh</td>
                                                <td>{dataProduct.thongTinBaoHanh.thoiGianBaoHanh} {dataProduct.thongTinBaoHanh.donViBaoHanh === 0 ? 'thng' : 'nm'}</td>
                                            </tr>
                                        </Fragment>
                                    )
                                }
                            </tbody>
                        </Table>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}
