import React, { useEffect, useState, Fragment } from 'react'
import { useDispatch } from 'react-redux'
import { axios } from '../../config/constant';
import { Steps, List, message } from 'antd';
import { Image } from 'react-bootstrap';

export default function DonHangDetail(props) {
    const dispatch = useDispatch();
    const idDonHang = props.match.params.id;
    const [dataChiTietDonHang, setDataChiTietDonHang] = useState([]);
    const [showChiTiet, setShowChiTiet] = useState([]);
    const { Step } = Steps;
    const [dataLichSu, setDataLichSu] = useState([]);

    function format_curency(a) {
        a = a.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
        return a;
    }

    function hamChuyenDoiNgay(date) {
        var strDate = '';
        var now = new Date();
        var ngay = date.getDate().toString();
        var thang = (date.getMonth() + 1).toString();
        var nam = date.getFullYear().toString();

        strDate = ngay + '/' + thang + '/' + nam;
        return strDate;
    }

    async function LayChiTietDonHangTheoIDDonHang(idDonHang) {
        let res = await axios('hethong/order-details?idOrder=' + idDonHang);

        if (res.data.status === 'success') {
            setDataChiTietDonHang(res.data.data);
        } else {
            message.error('Ly data chi tit n hng tht bi');
        }
    }

    async function LayLichSuChiTietDonHang() {
        let res = await axios.get('hethong/lichsu-ctdonhang');

        if (res.data.status === 'success') {
            setDataLichSu(res.data.data);
        } else {
            message.error('Ly data lch s tht bi');
        }
    }

    useEffect(() => {
        dispatch({ type: 'SHOW_HEADER' });
        LayChiTietDonHangTheoIDDonHang(idDonHang);
        LayLichSuChiTietDonHang();
    }, []);

    useEffect(() => {
        for (let index = 0; index < dataChiTietDonHang.length; index++) {
            showChiTiet.push(false);
        }
    }, [dataChiTietDonHang])

    console.log(showChiTiet);

    return (
        <div className='container'>
            <div className='row'>
                <span style={{ fontSize: 20 }}>Chi tit n hng {idDonHang}</span>
                <br></br>
            </div>

            <div style={{ backgroundColor: 'white', width: '100%' }}>
                <table className="table">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th>Tn sn phm</th>
                            <th>Thnh tin</th>
                            <th>Trng thi</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            dataChiTietDonHang.map((item, i) => {
                                return <Fragment key={i}>
                                    <tr>
                                        <td style={{ width: 140 }}>{item.idShow}</td>
                                        <td style={{ width: 380 }}>{item.ten}</td>
                                        <td style={{ width: 140 }}>{format_curency(item.thanhTien.toString())} <u></u></td>
                                        <td style={{ width: 100 }}>
                                            {
                                                item.trangThai === 0 && (
                                                    <span style={{ color: 'blue' }}><strong>Ch tip nhn t shop</strong></span>
                                                )
                                            }
                                            {
                                                item.trangThai === 1 && (
                                                    <span style={{ color: 'red' }}><strong>Shop  tip nhn</strong></span>
                                                )
                                            }
                                            {
                                                item.trangThai === 2 && (
                                                    <span style={{ color: '#CC00FF' }}><strong>ang vn chuyn</strong></span>
                                                )
                                            }
                                            {
                                                item.trangThai === 3 && (
                                                    <span style={{ color: '#006600' }}><strong> nhn hng</strong></span>
                                                )
                                            }
                                            {
                                                item.trangThai === 4 && (
                                                    <span style={{ color: 'green' }}><strong>Hon thnh</strong></span>
                                                )
                                            }
                                            {
                                                item.trangThai === 5 && (
                                                    <Fragment>
                                                        <span style={{ color: 'black' }}><strong>Hy</strong></span><br></br>
                                                        <span style={{ color: 'red' }}>({item.ghiChu})</span>
                                                    </Fragment>
                                                )
                                            }
                                        </td>
                                        <td>
                                            <a href='/' onClick={(e) => {
                                                e.preventDefault();
                                                if (showChiTiet[i] === true) {
                                                    showChiTiet[i] = false;
                                                    setShowChiTiet([
                                                        ...showChiTiet,
                                                    ])
                                                } else {
                                                    showChiTiet[i] = true;
                                                    setShowChiTiet([
                                                        ...showChiTiet,
                                                    ])
                                                }
                                            }}>
                                                {
                                                    showChiTiet[i] === true ? (
                                                        <strong>
                                                            ng <i class="fa fa-angle-up"></i>
                                                        </strong>
                                                    ) : (<strong>
                                                        Xem chi tit <i class="fa fa-angle-down"></i>
                                                    </strong>)
                                                }
                                            </a>
                                        </td>
                                    </tr>
                                    {
                                        showChiTiet[i] === true && dataChiTietDonHang.length > 0 && (
                                            <tr>
                                                <td colSpan={8} style={{ height: 'auto', backgroundColor: '#DDDDDD' }}>
                                                    <div className='col'>
                                                        <div className='row'>
                                                            <div className='col-sm-5'>
                                                                <div className='row' style={{ height: 160, paddingTop: 10 }}>
                                                                    <Image src={item.img} style={{ width: 80, height: 80, marginLeft: 20 }}></Image>
                                                                    <div className='col-sm-6' style={{ marginLeft: 20 }}>
                                                                        <strong>
                                                                            {
                                                                                item.mauSac !== '' && item.size !== '' && (
                                                                                    item.ten + ' - ' + item.mauSac + ' - ' + item.size
                                                                                )
                                                                            }
                                                                            {
                                                                                item.mauSac === '' && item.size !== '' && (
                                                                                    item.ten + ' - ' + item.size
                                                                                )
                                                                            }
                                                                            {
                                                                                item.mauSac !== '' && item.size === '' && (
                                                                                    item.ten + ' - ' + item.mauSac
                                                                                )
                                                                            }
                                                                            {
                                                                                item.size === '' && item.mauSac === '' && (
                                                                                    item.ten
                                                                                )
                                                                            }
                                                                        </strong>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className='col-sm-2'>
                                                                <strong>Gi</strong><br></br>
                                                                {format_curency(item.giaCuoiCung.toString())} <u></u>
                                                            </div>
                                                            <div className='col-sm-1'>
                                                                <strong>S lng</strong><br></br>
                                                                {item.soLuong}
                                                            </div>
                                                            <div className='col-sm-2'>
                                                                <strong>Gim gi</strong><br></br>
                                                                {format_curency(item.giamGia.toString())} <u></u>
                                                            </div>
                                                            <div className='col-sm-2'>
                                                                <strong>Tm tnh</strong><br></br>
                                                                {format_curency(item.thanhTien.toString())} <u></u>
                                                            </div>
                                                        </div>
                                                        <div className='col'>
                                                            <span>TRNG THI N HNG</span>
                                                            <div style={{ width: '100%', height: 90, backgroundColor: 'white', borderRadius: 5, padding: 10 }}>
                                                                <br></br>
                                                                <Steps size="small" current={item.trangThai}>
                                                                    <Step title="t hng thnh cng" />
                                                                    <Step title="Shop  tip nhn" />
                                                                    <Step title="ang vn chuyn" />
                                                                    <Step title=" nhn hng" />
                                                                    <Step title="Hon thnh" />
                                                                </Steps>
                                                            </div>
                                                            <br></br>
                                                            <br></br>
                                                            <span>CHI TIT TRNG THI N HNG</span>
                                                            <div style={{ width: '100%', height: 'auto', backgroundColor: 'white', borderRadius: 5, padding: 10 }}>
                                                                <List bordered>
                                                                    {
                                                                        dataLichSu.map((item2, i2) => {
                                                                            if (item2.idOrderDetail === item.idShow) {
                                                                                return <List.Item key={i2}>
                                                                                    <div className='row' style={{ width: '100%' }}>
                                                                                        <div className='col-sm-6'>
                                                                                            {
                                                                                                item2.trangThai === 0 && (<strong>t hng thnh cng</strong>)
                                                                                            }
                                                                                            {
                                                                                                item2.trangThai === 1 && (<strong>Shop  tip nhn</strong>)
                                                                                            }
                                                                                            {
                                                                                                item2.trangThai === 2 && (<strong>ang vn chuyn</strong>)
                                                                                            }
                                                                                            {
                                                                                                item2.trangThai === 3 && (<strong> nhn hng</strong>)
                                                                                            }
                                                                                            {
                                                                                                item2.trangThai === 4 && (<strong>Hon thnh</strong>)
                                                                                            }
                                                                                            {
                                                                                                item2.trangThai === 5 && (<strong>Hy</strong>)
                                                                                            }
                                                                                        </div>
                                                                                        <div className='col-sm-6'>
                                                                                            {
                                                                                                item2.ngayThucHien !== '' ? hamChuyenDoiNgay(new Date(item2.ngayThucHien)) : '...'
                                                                                            }
                                                                                        </div>
                                                                                    </div>
                                                                                </List.Item>
                                                                            }

                                                                        })
                                                                    }

                                                                </List>
                                                            </div>
                                                        </div>
                                                    </div>

                                                </td>
                                            </tr>
                                        )
                                    }
                                </Fragment>
                            })
                        }

                    </tbody>
                </table>
            </div>
        </div>
    )
}
