import React, { Fragment, useState, useEffect } from 'react';
import { NavDropdown } from 'react-bootstrap';
import { useCookies } from 'react-cookie';
import { useSelector, useDispatch } from 'react-redux';
import { axios } from '../../config/constant';
import { useHistory } from 'react-router-dom';
import { message } from 'antd';

export default function UserComponent() {
    const [cookies, setCookies, removeCookie] = useCookies();
    const isAdminReducer = useSelector(state => state.isAdmin);
    const [dataEmail, setDataEmail] = useState('');
    var history = useHistory();

    async function LayDataUserTheoID(userID) {
        let res = await axios.get('hethong/users-item?idUser=' + userID);
        if (res.data.status === 'success') {
            setDataEmail(res.data.data.email);
        } else {
            message.error('Ly data user tht bi');
        }
    }


    useEffect(() => {
        LayDataUserTheoID(cookies.userID);
    }, [])

    return (
        <Fragment>
            <NavDropdown title={dataEmail.length > 0 ? dataEmail : ''} id="basic-nav-dropdown">
                {
                    isAdminReducer && (
                        <NavDropdown.Item onClick={() => {
                            history.push('/admin');
                        }}>Trang qun l</NavDropdown.Item>
                    )
                }
                <NavDropdown.Item onClick={() => {
                    history.push('/customer/account');
                }}>Ti khon ca ti</NavDropdown.Item>
                {
                    !isAdminReducer && (
                        <Fragment>
                            <NavDropdown.Item onClick={() => {
                                history.push('/customer/order');
                            }}>n hng ca ti</NavDropdown.Item>
                            <NavDropdown.Item onClick={() => {
                                history.push('/customer/payment');
                            }}>Thng tin thanh ton</NavDropdown.Item>
                            <NavDropdown.Item onClick={() => {
                                history.push('/customer/comment');
                            }}>Nhn xt ca ti</NavDropdown.Item>
                            <NavDropdown.Item onClick={() => {
                                history.push('/customer/san-pham-da-xem');
                            }}>Sn phm  xem</NavDropdown.Item>
                        </Fragment>
                    )
                }
                <NavDropdown.Item onClick={() => {
                    removeCookie('token');
                    removeCookie('userID');
                    localStorage.setItem('email', 'undefined');
                    window.location.pathname = '/';
                }}>ng xut</NavDropdown.Item>
            </NavDropdown>
        </Fragment>
    )
}
