import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { axios } from '../../config/constant';
import { Result } from 'antd';
import { Link } from 'react-router-dom';
import { Button } from 'react-bootstrap';

export default function ResultSuccessPage(props) {
    const donhangID = props.match.params.id;
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch({ type: 'CLOSE_HEADER' });
    }, [])

    return (
        <div>
            <Result
                status="success"
                title=" t hng thnh cng"
                subTitle={"M n hng: " + donhangID + ". T by gi bn c th theo di n hng ca mnh."}
                extra={[
                    <Link to='/'>
                        <Button>Tr v Trang Ch</Button>
                    </Link>
                ]}>

            </Result>
        </div>
    )
}
