import React, { useState, useEffect } from 'react';
import { Modal, Image, Spinner } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { Form, Input, Button, Select, message } from 'antd';
import { axios } from '../../config/constant';
import { storage } from '../../firebase/firebase';
import { QuillEditor } from '../allJS';

export default function ModalThemBaiViet() {
    const { Option } = Select;
    const [files, setFiles] = useState([])
    const [firstTime, setFirstTime] = useState(true);
    const [dataProduct, setDataProduct] = useState([]);
    const [imageAsFile, setImageAsFile] = useState([]);
    const [statusMessageError, setStatusMessageError] = useState(-1);
    const [countAnhDaUploadThanhCong, setCountAnhDaUploadThanhCong] = useState(0);
    const showModalThemBaiViet = useSelector(state => state.showModalThemBaiViet);
    const [spinnerThemBaiViet, setSpinnerThemBaiViet] = useState(-1);
    const dispatch = useDispatch();
    const [dataThem, setDataThem] = useState({
        tieuDe: "",
        img: "",
        ngayTao: new Date(),
        loaiBaiViet: -1,
        idProducts: [],
        content: '',
        luotXem: 0,
        isLock: false,
        isDelete: false
    })

    const onEditorChange = (value) => {
        setDataThem({
            ...dataThem,
            content: value
        })
    }

    const onFilesChange = (files) => {
        setFiles(files)
    }
    console.log(dataThem);

    const handleChange = (e) => {
        var soLuongFile = e.target.files.length;
        var listFile = [];
        var listUrl = [];
        for (let index = 0; index < soLuongFile; index++) {
            listFile.push(e.target.files[index]);
        }

        setImageAsFile(listFile);

        if (listFile.length === 0) {
            console.log('Khng c file no c upload');
        } else {
            for (let index = 0; index < soLuongFile; index++) {
                console.log('start of upload');
                // async magic goes here...
                if (listFile[index] === '') {
                    console.error(`not an image, the image file is a ${typeof (listFile[index])}`);
                }
                const uploadTask = storage.ref(`/images/${listFile[index].name}`).put(listFile[index]);
                uploadTask.on('state_changed',
                    (snapShot) => {
                        //takes a snap shot of the process as it is happening
                        console.log(snapShot);
                    }, (err) => {
                        //catches the errors
                        console.log(err)
                    }, () => {
                        // gets the functions from storage refences the image storage in firebase by the children
                        // gets the download url then sets the image from firebase as the value for the imgUrl key:
                        storage.ref('images').child(listFile[index].name).getDownloadURL()
                            .then(fireBaseUrl => {
                                // setImageAsUrl(prevObject => ({ ...prevObject, imageAsUrl: fireBaseUrl }))
                                setDataThem({
                                    ...dataThem,
                                    img: fireBaseUrl
                                })
                                listUrl.push(fireBaseUrl);
                                setCountAnhDaUploadThanhCong(countPrev => countPrev + 1);
                            })
                    })
            }
        }
    }

    function KiemTraDuLieuNhap(data) {
        if (data.tieuDe.trim().length === 0 || data.img === '' || data.loaiBaiViet === -1 || data.content.trim().length === 0) {
            setStatusMessageError(0);
        } else {
            ThemBaiViet()
            setStatusMessageError(-1);
        }
    }

    async function ThemBaiViet() {
        setSpinnerThemBaiViet(1);
        setDataThem({
            ...dataThem,
            content: ''
        });

        let res = await axios.post('hethong/baiviet-them', {
            data: dataThem
        });

        if (res.data.status === 'success') {
            message.success(' to bi vit mi thnh cng');
            dispatch({ type: 'CLOSE_THEM_BAIVIET' });
            dispatch({ type: 'RELOAD_DATABASE' });
            setSpinnerThemBaiViet(0);
        } else {
            message.error(res.data.message);
            dispatch({ type: 'CLOSE_THEM_BAIVIET' });
            dispatch({ type: 'NO_RELOAD_DATABASE' });
            setSpinnerThemBaiViet(0);
        }
    }

    async function LayDataSanPham() {
        let res = await axios.get('hethong/products');

        if (res.data.status === 'success') {
            setDataProduct(res.data.data);
        } else {
            message.error('Ly data sn phm tht bi !');
        }
    }

    useEffect(() => {
        if (firstTime === false) {
            if (imageAsFile.length === 0) {
                message.success('Vui lng chn nh i din cho bi vit')
            } else {
                if (countAnhDaUploadThanhCong === imageAsFile.length) {
                    message.error('Upload nh i din bi vit thnh cng');
                }
            }
        }
    }, [countAnhDaUploadThanhCong])

    console.log(dataThem);
    console.log(dataProduct);

    return (
        <Modal show={showModalThemBaiViet} size="lg" animation={false} onHide={() => {
            dispatch({ type: 'CLOSE_THEM_BAIVIET' });
        }}
            onShow={() => {
                LayDataSanPham();
                setImageAsFile([]);
                setFirstTime(true);
                setStatusMessageError(-1);
                setCountAnhDaUploadThanhCong(0);
                setDataThem({
                    tieuDe: "",
                    img: "",
                    ngayTao: new Date(),
                    loaiBaiViet: -1,
                    idProducts: [],
                    content: '',
                    luotXem: 0,
                    isLock: false,
                    isDelete: false
                })
            }}>
            <Form
                name="basic"
                layout='vertical'
                initialValues={{ remember: true }}
                style={{ padding: 40 }}>
                <Form.Item
                    label="Tiu "
                    name="tieude"
                    rules={[{ required: true, message: 'Vui lng nhp tn tiu  bi vit ' }]}>
                    <Input onChange={(e) => {
                        setDataThem({
                            ...dataThem,
                            tieuDe: e.target.value
                        })
                    }} />
                </Form.Item>

                <Form.Item
                    label="nh i din"
                    name="username"
                    rules={[{ required: true, message: 'Vui lng chn nh' }]}>
                    <input type='file'
                        onChange={(e) => {
                            handleChange(e);
                            setCountAnhDaUploadThanhCong(0);
                            setFirstTime(false);
                        }}>
                    </input>
                </Form.Item>

                <Form.Item>
                    {
                        dataThem.img === '' ? <Image alt="nh show" src="https://www.generationsforpeace.org/wp-content/uploads/2018/03/empty.jpg" style={{ width: 300, height: 200 }}></Image> : <Image alt="nh show" src={dataThem.img} style={{ width: 300, height: 200 }}></Image>
                    }
                </Form.Item>

                <Form.Item
                    label="Loi bi vit"
                    name="loaibaiviet"
                    rules={[{ required: true, message: 'Vui lng chn loi bi vit' }]}>
                    <Select value={dataThem.loaiBaiViet} onChange={(value) => {
                        setDataThem({
                            ...dataThem,
                            loaiBaiViet: value
                        })
                    }}>
                        <Option value={-1}>Chn loi bi vit</Option>
                        <Option value={0}>Bi vit v chng trnh/s kin</Option>
                        <Option value={1}>Bi vit gii thiu</Option>
                    </Select>
                </Form.Item>

                {
                    dataThem.loaiBaiViet === 0 && (
                        <Form.Item
                            label="Cc sn phm lin quan n chng trnh/s kin"
                            name="sanphams"
                            rules={[{ required: true, message: 'Vui lng chn cc sn phm' }]}>
                            <Select
                                showSearch
                                optionFilterProp="children"
                                filterOption={(input, option) =>
                                    option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                                mode="multiple"
                                style={{ width: '100%' }}
                                placeholder="Chn sn phm"
                                onChange={(value) => {
                                    setDataThem({
                                        ...dataThem,
                                        idProducts: value
                                    })
                                }}>
                                {
                                    dataProduct.map((item, i) => {
                                        return <Option key={item._id} value={item._id}>
                                            {item.ten}
                                        </Option>
                                    })
                                }
                            </Select>
                        </Form.Item>
                    )
                }

                <Form.Item
                    label="Ni dung"
                    name="noidung">

                    <QuillEditor
                        placeholder={"Nhp ni dung"}
                        onEditorChange={onEditorChange}
                        onFilesChange={onFilesChange} />

                </Form.Item>

                <Form.Item>
                    {
                        statusMessageError === 0 && (
                            <p style={{ color: 'red', lineHeight: 1.5 }}>Vui lng nhp  d liu cn thit</p>
                        )
                    }
                    <Button type="primary" style={{ marginLeft: '30%', width: 300, height: 50 }} onClick={() => {
                        KiemTraDuLieuNhap(dataThem);
                    }}>
                        {
                            spinnerThemBaiViet === 1 ?
                                <Spinner animation="border" role="status">
                                    <span className="sr-only">Loading...</span>
                                </Spinner> : "Thm"

                        }
                    </Button>
                </Form.Item>
            </Form>
        </Modal>
    )
}
