import React, { Fragment, useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { axios } from '../../config/constant';
import { useCookies } from 'react-cookie';
import { message } from 'antd';

export default function Customer_ThongTinThanhToan() {
    const dispatch = useDispatch();
    const [dataDonHang, setDataDonHang] = useState([]);
    const [cookies, setCookie, removeCookie] = useCookies();

    function format_curency(a) {
        a = a.replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1.");
        return a;
    }

    async function LayDataDonHangTheoIDUser(idUser) {
        let result = await axios.get('hethong/orders-user?id=' + idUser);
        if (result.data.status === 'success') {
            setDataDonHang(result.data.data);
        } else {
            message.error("Ly d liu data n hng tht bi");
        }
    }

    useEffect(() => {
        dispatch({ type: 'SHOW_HEADER' });
        LayDataDonHangTheoIDUser(cookies.userID);
    }, [])

    return (
        <Fragment>
            <div className='container'>
                <div className='row'>
                    <span style={{ fontSize: 20 }}>Thng tin thanh ton ({dataDonHang.length} kt qu)</span>
                    <br></br>
                </div>

                <div style={{ backgroundColor: 'white', width: '100%' }}>
                    <table className="table">
                        <thead>
                            <tr>
                                <th>STT</th>
                                <th>Ni dung thanh ton</th>
                                <th>Hnh thc thanh ton</th>
                                <th>Tng tin</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                dataDonHang.map((item, i) => {
                                    return <tr key={i}>
                                        <td>{i + 1}</td>
                                        <td>Thanh ton cho n hng <Link to={'/customer/order/details/' + item.idShow}>{item.idShow}</Link></td>
                                        <td style={{ width: 250 }}>
                                            {
                                                item.hinhThucThanhToan === 0 && ('Thanh ton khi nhn hng')
                                            }
                                            {
                                                item.hinhThucThanhToan === 1 && ('Thanh ton bng Paypal')
                                            }
                                            {
                                                item.hinhThucThanhToan === 2 && ('Thanh ton bng th ATM ni a / Internet Banking')
                                            }
                                        </td>
                                        <td style={{ width: 150 }}>{format_curency(item.tongTien.toString())} <u></u></td>
                                    </tr>
                                })
                            }

                        </tbody>
                    </table>
                </div>
            </div>
        </Fragment>

    )
}
